<template>
    <div id="blog-nav">
        <ul>
            <li>
                <router-link to="/blog/">博客首页</router-link>
            </li>
            <li>
                <router-link to="/blog/add">添加博客</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'blog-nav'
    }
</script>

<style scoped>
div{
    background: #2f2f2f;
    padding: 20px;
}
ul{
    list-style: none;
    text-align: center;
}
ul li{
    display: inline-block;
    margin: 0 20px;
}
ul li a{
    display: block;
    padding: 10px 20px;
    text-decoration: none;
    color: #fff;
    background: #ccc;
    border-radius: 5px;
}
.router-link-exact-active{
    background: #abcdef;
}
</style>


