<template>
    <div id="single-blog">
        <h1>{{title | toUpperCase}}</h1>
        <p>{{content}}</p>
        <router-link :to="'/blog/edit/'+id">编辑</router-link>
        <button @click='remove'>删除</button>
    </div>
</template>

<script>
export default {
    name: 'single-blog',
    data(){
        return {
            id: this.$route.params.id,
            title: '',
            content: ''
        }
    },
    created(){
        // this.axios.get('/posts/'+this.id)
        // .then((res) => {
        //     console.log(res);
        //     this.title = res.body.title;
        //     this.body = res.body.body;
        // });
        
        const query = Bmob.Query('blogs');
        query.get(this.id).then(res => {
            console.log(res)
            this.title = res.title;
            this.content = res.content;
        }).catch(err => {
            console.log(err)
        })
    },
    filters: {
        toUpperCase(value){
            if(!value) return
            value = value.toString();
            return value.toUpperCase();
        }
    },
    methods: {
        remove(){
            const query = Bmob.Query('blogs');
            query.destroy(this.id).then(res => {
                console.log(res)
                if(res.msg == 'ok'){
                    alert('删除成功');
                    window.location.href = '/blog/'
                }
            }).catch(err => {
                console.log(err)
            })
        }
    }
}
</script>

<style scoped>
#single-blog{
    max-width: 800px;
    margin: 40px auto;
    background: #eee;
    padding: 10px 20px 40px;
    box-sizing: border-box;
}
button{
    display: inline-block;
    padding: 10px 20px;
    background: #f00;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px 0;
    outline: none;
    font-size: 13px;
}
a{
    text-decoration: none;
    color: #fff;
    background: #007def;
    border-radius: 5px;
    display: inline-block;
    display: inline-block;
    padding: 10px 20px;
    font-size: 13px;
    margin-right: 40px;
}
</style>


