<template>
  <div id="edit-blog">
    <form action="javascript:;" v-if="!submited">
      <div>
        <label>博客标题</label>
        <input type="text" v-model="blog.title">
      </div>
      <div>
        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>
      </div>
      <!-- <div class="checkbox">
        <label>vue.js <input type="checkbox" value="vue.js" v-model="blog.type"></label>
        <label>node.js <input type="checkbox" value="node.js" v-model="blog.type"></label>
         <label>angular.js <input type="checkbox" value="angular.js" v-model="blog.type"></label>
      </div>
      <div>
        <label>作者</label>
        <select v-model="blog.author">
          <option value="">请选择作者</option>
          <option v-for="(item,index) in authors" :key="index">{{item}}</option>
        </select>
      </div> -->
      <div>
        <button @click="submit">提交</button>
      </div>
    </form>
    <h2 v-if="submited">博客提交成功</h2>
    
    <div v-if="submited">
      <p>博客标题：{{blog.title}}</p>
      <p>博客标题：{{blog.content}}</p>
      <!-- <p>分类：</p>
      <div v-for="(item, index) in blog.type" :key="index">
        <p>{{item}}</p>
      </div>
      <p>作者：{{blog.author}}</p> -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'edit-blog',
  data () {
    return {
      id: this.$route.params.id,
      blog: {
        title: '',
        content: '',
        type: [],
        author: ''
      },
      authors: ['张三', '李四', '王五'],
      submited: false
    }
  },
  created(){
      const query = Bmob.Query('blogs');
      query.get(this.id).then(res => {
        console.log(res)
        this.blog.title = res.title
        this.blog.content = res.content
      }).catch(err => {
        console.log(err)
      })
  },
  methods: {
    submit: function(){
      // this.axios.post('/posts', {
      //   title: this.blog.title,
      //   body: this.blog.content,
      //   userId: 1
      // })
      // .then((res) => {
      //   console.log(res)
      //   this.submited = true;
      // })

      const query = Bmob.Query('blogs');
      query.set("id", this.id)  //需要修改的id
      query.set("title", this.blog.title)
      query.set("content", this.blog.content)
      query.save().then(res => {
        console.log(res)
        this.submited = true;
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#edit-blog{
  max-width: 540px;
  margin: 0 auto;
  padding: 30px 0;
}
label{
  display: block;
  padding: 0 0 10px;
}
input[type=text],textarea,select{
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
textarea{
  height: 200px;
  padding: 10px;
}
form>div{
  margin-bottom: 20px;
}
.checkbox label{
  display: inline-block;
}
.checkbox input{
  margin-right: 20px;
}
button{
  display: block;
  width: 200px;
  height: 40px;
  background: #aabbcc;
  border: none;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

</style>



