<template>
  <div id="show-blog" v-theme="'small'">
    <h1>博客预览</h1>
    <input type="text" v-model="search" id="search" placeholder="搜索">
    <div class="blog-list" v-for="(item,index) in filteredBlogs" :key="index">
        <router-link :to="'/blog/blog/' + item.objectId">
        <h2 v-colour>{{item.title | toUpperCase}}</h2>
        <p>{{item.content | toSlice}}</p>
        </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blog",
  data() {
    return {
      blogs: [],
      search: ''
    };
  },
  created() {
    //http://jsonplaceholder.typicode.com/posts
    // this.$http.get("../../static/posts.json").then(function(res) {
    //   this.blogs = res.body.slice(0, 10);
    // });
    // this.axios.get('/posts').then((res) => {
    //   console.log(res);
    //   this.blogs = res.data.slice(0, 10);
    // })
    const query = Bmob.Query("blogs");
    query.find().then(res => {
        console.log(res)
        this.blogs = res
    });
  },
  filters: {
    toUpperCase(value) {
      if (!value) return "";
      value = value.toString();
      return value.toUpperCase();
    },
    toSlice(value) {
      if (!value) return "";
      value = value.toString();
      return value.substring(0, 100) + "...";
    }
  },
  directives: {
    colour: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .substring(2, 8);
      }
    },
    theme: {
      bind(el, binding, vnode) {
        if (binding.value == "big") {
          el.style.maxWidth = "1280px";
        } else if (binding.value == "small") {
          el.style.maxWidth = "560px";
        }
      }
    }
  },
  computed: {
      filteredBlogs: function(){
          //箭头函数无需保存this
          //var that = this;
          return this.blogs.filter((blog) =>{
              return blog.title.match(this.search)
          })
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blog {
  max-width: 800px;
  margin: 0 auto;
}
.blog-list {
  background: #eee;
  padding: 20px;
  margin-bottom: 20px;
}
#search{
    display: block;
    width: 100%;
    height: 40px;
    padding: 0 10px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 20px;
}
a{
  text-decoration: none;
}
p{
  color: #444;
}
</style>



